<template>
  <div :class="{baner : true, siva: coloredBackground}">
    <div class="trougao">
      <div :class="{oblik: true, siv: coloredTriangle}"></div>
    </div>
    <div :class="odd ? 'inner' : 'inner inverse'">
      <div data-aos="fade-in" v-if="!eng">
        <h2>{{item.srpski.naslov}}</h2>
        <p>{{item.srpski.opis}}</p>
      </div>
      <div data-aos="fade-in" v-else>
        <h2>{{item.english.title}}</h2>
        <p>{{item.english.description}}</p>
      </div>
      <div :class="odd ? 'from-left' : 'from-right' ">
        <img v-if="odd" :src="require(`@/assets/${item.image}`)" data-aos="fade-left" />
        <img v-else :src="require(`@/assets/${item.image}`)" data-aos="fade-right" />
      </div>
    </div>
    <div class="clearfix"></div>
  </div>
</template>

<script>
export default {
  name: "ServiceItem",
  props: {
    item: Object,
    eng: Boolean,
    odd: Boolean,
  },
  data: function () {
    return {
      coloredBackground: false,
      coloredTriangle: false,
    };
  },
  methods: {
    colorBackground: function () {
      let self = this;
      setInterval(function () {
        self.coloredBackground = true;
      }, 1000);
    },
    colorTriangle: function () {
      let self = this;
      setInterval(function () {
        self.coloredTriangle = true;
      }, 1000);
    },
  },
  created() {
    if (this.odd) {
      this.colorBackground();
    } else {
      this.colorTriangle();
    }
  },
};
</script>

<style scoped>
.baner {
  position: relative;
  transition: all 1s;
}

.trougao {
  position: absolute;
  top: 0px;
  width: 100%;
}

.trougao div {
  margin: auto;
  border-color: white;
  width: 0;
  height: 0;
  border-left: 50px solid transparent;
  border-right: 50px solid transparent;
  transition: border-color 1s;
}

.oblik {
  border-top: 50px solid white;
}
.siv {
  border-top: 50px solid #f3f3f3 !important;
}

.baner .inner {
  width: 1170px;
  margin: auto;
}

.baner .inner div {
  text-align: center;
  float: left;
  width: 50%;
  padding: 20px;
  vertical-align: middle;
}

.baner .inverse div {
  float: right;
}

.clearfix {
  float: none;
  clear: both;
}

.baner .inner div img {
  width: 80%;
  margin: 25px;
}

.siva {
  background-color: #f3f3f3;
}

.from-right .before-enter {
  opacity: 0;
  margin-left: 100px;
  transition: all 1.5s ease;
}

.from-left .before-enter {
  opacity: 0;
  margin-left: -100px;
  transition: all 1.5s ease;
}

.from-right .enter,
.from-left .enter {
  opacity: 1;
  margin-left: 0;
}

@keyframes text-animacija {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@media only screen and (max-width: 1170px) {
  .baner .inner {
    width: 100%;
  }
}

@media only screen and (max-width: 840px) {
  .baner .inner div {
    display: block;
    width: 100%;
    float: none;
  }

  .inverse {
    float: none;
  }

  .trougao div {
    border-left: 25px solid transparent;
    border-right: 25px solid transparent;
  }

  .oblik {
    border-top: 25px solid white;
  }
  .siv {
    border-top: 25px solid #f3f3f3 !important;
  }
}
</style>