<template>
  <main class="services">
    <PageTitle :eng="engleski" :srpski="'Usluge'" :engleski="'Services'" />

    <ServiceItem
      v-for="(item,i) in services"
      :key="i"
      :odd="isOdd(i)"
      :item="item"
      :eng="engleski"
    />
  </main>
</template>


<script>
import PageTitle from "../components/Shared/PageTitle.vue";
import ServiceItem from "../components/Services/ServiceItem.vue";
import MY_JSON from "../data/services.json";

export default {
  name: "Services",
  props: {
    engleski: Boolean,
  },
  components: {
    PageTitle,
    ServiceItem,
  },
  methods: {
    isOdd: (i) => i % 2 == 0,
  },
  data: function () {
    return {
      prikazSive: false,
      services: [],
    };
  },
  created: function () {
    this.services = MY_JSON;
  },
};
</script>



<style scoped>
.services {
  padding-bottom: 120px;
}
